import Header from "./Header";
import styled from 'styled-components';

const StyledHomePage = styled.div`
    display: grid;
    grid-template-columns: repeat(21, 1fr);
    grid-template-rows: repeat(21, 1fr);
    height: 100vh;
`;

const HomePage = () => (
  <StyledHomePage>   
    <Header />
  </StyledHomePage>
);

export default HomePage;
